import "../css/signup.css";

function logout() {
  alert("logout");
}

export function Signup() {
  return (
    <form className="form">
      <label htmlFor="fname">Email: </label>
      <input type="text" name="email" id="email" />
      <br />
      <br />
      <label htmlFor="fname">EPassword: </label>
      <input type="ePassword" name="ePass" id="ePass" /> <br />
      <br />
      <div id="rmsg">
        <button onClick={() => logout()}>log out</button>
      </div>
    </form>
  );
}
