import React, { Component } from "react";
import employee from "../images/employee.jpg";
import '../css/employee.css';
import { Link } from "react-router-dom";


class EmployeeCard extends Component {
  constructor(props) {
    super(props);
  }
  state = {};
  render() {
    console.log(this.props)
    return (
      <div className="div">
        <img src={employee} width="100%" height="100%"/>
        <div className="a">
          <p>{this.props.name}</p>
          <p>{this.props.id}</p>
          {/* <EmployeeDetails name="shoba" />
            <EmployeeDetails name="shoba" />
            <EmployeeDetails name="shoba" />
            <EmployeeDetails name="shoba" /> */}

          <Link className="btn" to={'/employeeList'} >
            Back
          </Link>
        </div>
      </div>
    );
  }
}

export default EmployeeCard;
