import React, { Component } from "react";
import "../css/pagentionreact.css";
import "../css/table.css";

class Table extends Component {
  constructor(props) {
    console.log("constructor");
    super(props);
  }
  state = {
    isdata: false,
    pagelimit: 5,
    page: 1,
  };
  data = [];
  filterData = [];

  displayHead() {
    console.log("displayHead");
    if (this.state.isdata) {
      let keys = Object.keys(this.data[0]);
      return keys.map((eachKey) => <th key={eachKey}>{eachKey}</th>);
    } else {
      return null;
    }
  }

  eachRow(data) {
    let keys = Object.keys(data);
    return keys.map((eachKey) => <td key={eachKey}>{data[eachKey]}</td>);
  }

  displayBody() {
    if (this.state.isdata) {
      console.log("displayBody");
      return this.filterData.map((eachKey) => (
        <tr key={"row" + eachKey.id}>{this.eachRow(eachKey)}</tr>
      ));
    } else {
      return null;
    }
  }
  displayPage() {
    let start = (this.state.page - 1) * this.state.pagelimit;
    let end = start + this.state.pagelimit;
    this.filterData = this.data.slice(start, end);
    return this.displayBody();
  }
  generateButton() {
    let pageButtons = [];
    let start = this.state.page - 2 < 1 ? 1 : this.state.page - 2;
    let end =
      this.state.page + 2 <= this.findLastpage()
        ? this.state.page + 2
        : this.findLastpage();
    for (let i = start; i <= end; i++) {
      pageButtons.push(i);
    }
    return pageButtons.map((eachButton) => {
      if (eachButton != this.state.page)
        return (
          <button
            key={"button" + eachButton}
            onClick={() => this.setState({ page: eachButton })}
          >
            {eachButton}
          </button>
        );
      else
        return (
          <button
            className="active"
            key={"button" + eachButton}
            onClick={() => this.setState({ page: eachButton })}
          >
            {eachButton}
          </button>
        );
    });
  }
  // enabledisablebutton(page) {
  //     if (page == 1) {
  //         document.getElementById("first").disabled = true;
  //         document.getElementById("prev").disabled = true;
  //         document.getElementById("next").disabled = false;
  //         document.getElementById("last").disabled = false;
  //     } else if(page==lastPage)            {
  //         document.getElementById("last").disabled = true;
  //         document.getElementById("next").disabled = true;
  //         document.getElementById("first").disabled = false;
  //         document.getElementById("prev").disabled = false;
  //     }
  //     else {
  //         document.getElementById("next").disabled = false;
  //         document.getElementById("last").disabled = false;
  //         document.getElementById("first").disabled = false;
  //         document.getElementById("prev").disabled = false;
  //     }
  // }
  findLastpage() {
    return Math.ceil(this.data.length / this.state.pagelimit);
  }

  componentDidMount() {
    console.log("componentDidMount");
    this.getData();
  }

  componentDidUpdate() {
    console.log("componentDidUpdate");
  }

  componentWillUnmount() {
    console.log("componentWillUnmount");
  }

  getData() {
    this.data = this.props.data;
    console.log(this.props.data);
    this.setState({ isdata: true });
  }

  changepagelimit(event){
    // alert(event.target.value);
    this.setState({pagelimit: event.target.value, page:1})
  }

  render() {
    console.log("render");
    let tableBody = <div>Loading ... </div>;
    let displayHeadings = this.displayHead();
    let displayBodyData = this.displayPage();
    let buttons = this.generateButton();

    if (this.state.isdata) {
      tableBody = (
        <table>
          <thead>
            <tr>{displayHeadings}</tr>
          </thead>
          <tbody>{displayBodyData}</tbody>
        </table>
      );
    }

    return (
      <div>
        <div>
          <button onClick={() => this.setState({ page: 1 })}>First</button>
          <button onClick={() => this.setState({ page: this.state.page - 1 })}>
            Pre
          </button>
          {buttons}
          <button
            onClick={() => this.setState({ page: this.state.page + 1 })}
            disabled={this.state.page == this.findLastpage()}
          >
            Next
          </button>
          <button onClick={() => this.setState({ page: this.findLastpage() })}>
            Last
          </button>
          <select name="pagelimit" id="pagelimit" onChange={(event)=>this.changepagelimit(event)}>
            <option value={5}>5</option>
            <option value={10}>10</option>
            <option value={15}>15</option>
            <option value={20}>20</option>
          </select>
        </div>

        {tableBody}
      </div>
    );
  }
}

export default Table;
