import React from 'react';
import '../css/calculator.css';


export class Calculator extends React.Component {
    view(input) {
        document.getElementById("calculator").value += input;
        // alert(input)
    };

    val() {
        try {
            document.getElementById("calculator").value = eval(document.getElementById("calculator").value);
        }
        catch (err) {
            alert("invalid")
        }

    }
    clr() {
        document.getElementById("calculator").value = "";

    }
    del() {
        document.getElementById("calculator").value = document.getElementById("calculator").value.slice(0, -1);

    }
    render() {


        return (

            <div id="cal">


                <div className="calc">
                    <input type="text" name="calculator" id="calculator" placeholder="0" />
                    <button onClick={() => this.view('1')}>1</button>
                    <button onClick={() => this.view('2')}>2</button>
                    <button onClick={() => this.view('3')}>3</button>
                    <button onClick={() => this.view('4')}>4</button>
                    <button onClick={() => this.view('5')}>5</button>
                    <button onClick={() => this.view('6')}>6</button>
                    <button onClick={() => this.view('7')}>7</button>
                    <button onClick={() => this.view('8')}>8</button>
                    <button onClick={() => this.view('9')}>9</button>
                    <button onClick={() => this.view('0')}>0 </button>
                    <button className="btn" onClick={() => this.view('+')} >+ </button>
                    <button className="btn" onClick={() => this.view('-')}>-</button>
                    <button className="btn" onClick={() => this.view('*')} >*</button>
                    <button className="btn" onClick={() => this.view('/')} >/</button>
                    <button className="btn" onClick={() => this.view('.')}>.</button>
                    <button className="btn" onClick={() => this.val()}>=</button>
                    <button className="btn" onClick={() => this.clr()}>Ac</button>
                    <button className="btn" onClick={() => this.del()} >Del</button>

                </div >
            </div >
        )
    }
}